import { useEffect, useState } from "react";
import { useFeedbackCreateStudentFeedback } from "../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormInput from "../../../Ui/Input/FormInput";
import { feedbackType } from "../../../Constant/Feedback/feedback_constant";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import InstituteDepartment from "../../../UserStaffMember/InstituteDepartment/InstituteDepartment";

const StudentFeedbackCreate = ({
  onClose,
  instituteId,
  onRefetch,
  isAuthority,
  onEditAction,
  feedback,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    feedback_name: "",
    feedback_type: "Range",
    how_many_question_option: "",
    department: [],
  });
  const [step, setStep] = useState("");

  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [feedbackCreateStudentFeedback] = useFeedbackCreateStudentFeedback();

  useEffect(() => {
    if (feedback) {
      let rt = [];
      if (feedback?.department?.length > 0) {
        for (let gt of feedback?.department) {
          rt.push(gt?._id);
        }
      }
      setState((prev) => ({
        ...prev,
        feedback_name: feedback?.feedback_name,
        feedback_type: feedback?.feedback_type,
        how_many_question_option: feedback?.how_many_question_option,
        department: rt,
      }));
    }
  }, [feedback]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onSelect = (value) => {
    setState((prev) => ({
      ...prev,
      how_many_question_option: value,
    }));
  };

  const onTile = (value) => {
    // setState((prev) => ({
    //   ...prev,
    //   incomeAccount: value,
    // }));
  };
  const onCreate = () => {
    let validation = requiredField(
      {
        feedback_name: state.feedback_name,
      },
      []
    );
    if (existingRequiredField(validation)) {
      if (isAuthority) {
        onEditAction(state);
      } else {
        if (instituteId) {
          setDisabled((pre) => !pre);
          feedbackCreateStudentFeedback({
            id: instituteId,
            studentFeedback: state,
          })
            .then(() => {
              onRefetch();
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch(() => {
              setDisabled((pre) => !pre);
            });
        }
      }
    } else {
      setErrorField(validation);
    }
  };

  const onCloseStep = () => {
    setStep("");
  };

  const onSelectDepartment = (val) => {
    if (state.department?.includes(val?._id)) {
      let ft = state?.department?.filter((dt) => dt !== val?._id);
      setState((prev) => ({
        ...prev,
        department: ft,
      }));
    } else {
      setState((prev) => ({
        ...prev,
        department: [...prev.department, val?._id],
      }));
    }
  };

  return (
    <>
      {step === 1 ? (
        <InstituteDepartment
          onClose={onCloseStep}
          onMultiSelect={onSelectDepartment}
          title={t("select_department")}
          instituteId={instituteId}
          whichType="Normal"
          isMultiSelect
          selectList={state.department}
          onDone={onCloseStep}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("create_student_feedback")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("feedback_name")}
                placeholder={t("feedback_name_placeholder")}
                name={"feedback_name"}
                value={state?.feedback_name}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["feedback_name"]}
              />
              <TileWrapper label="feedback_type">
                {feedbackType?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onTile}
                    activeTile={state.feedback_type}
                  />
                ))}
              </TileWrapper>
              <CustomSelect
                selectLabel={t("how_many_question_option")}
                selectedValue={t("how_many_question_option_placeholder")}
                defalutValue={state?.how_many_question_option}
                type={"text"}
                onClick={onSelect}
                options={[3, 4, 5]}
              />

              <CreateButton
                label={
                  state.department?.length > 0
                    ? "selected_particular_department"
                    : "select_particular_department"
                }
                customStyle={{
                  width: "fit-content",
                  padding: "0.4rem 1rem",
                }}
                onAction={() => setStep(1)}
                count={state.department?.length}
              />
              <CreateButton
                label={t(isAuthority ? "update" : "create_feedback")}
                customStyle={{
                  padding: "0.4rem 1rem",
                }}
                onAction={onCreate}
              />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default StudentFeedbackCreate;
